.programm {
  .offset(0; 0 0 30px 0);

  @media (min-width: @tablet-width) {
    margin-top: 82px;
    margin-bottom: 47px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 73px;
  }
}

.programm__wrapper {
  @media (min-width: 640px) and (max-width: 767px) {
    display: flex;
    flex-wrap: wrap;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
  }
}

.programm__section {
  .offset(30px 33px 15px 20px; 0 0 20px 0);
  background-color: @basic-grey;

  @media (min-width: 640px) and (max-width: 767px) {
    width: 290px;
    margin: 0 auto;
  }

  @media (min-width: @tablet-width) {
    .offset(77px 61px 53px 52px; 0 0 30px 0);
  }

  @media (min-width: @desktop-width) {
    width: 570px;
    .offset(77px 51px 55px 52px; 0 80px 0 0);
  }
}

.programm__section:first-of-type {
  @media (min-width: 640px) and (max-width: 767px) {
    margin-right: 20px;
  }
}

.programm__section:last-of-type {
  margin-bottom: 0;

  @media (min-width: @tablet-width) {
    .offset(79px 28px 55px 52px; 0);
  }

  @media (min-width: @desktop-width) {
    padding: 77px 27px 53px 52px;
  }
}

.programm__title {
  position: relative;
  .offset(0; 0 0 30px 71px);
  font-weight: 400;
  font-style: 24px;
  line-height: 37px;
  color: @black;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    margin: 0 0 26px 0;
    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 75px;
    padding-left: 162px;
  }
}

.programm__description {
  position: relative;
  .offset(0; 0 0 39px 0);
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;

  &::before {
    content: "";
    position: absolute;
    right: 0;
    top: -15px;
    background-repeat: no-repeat;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 52px;
    padding-right: 221px;
    font-size: 16px;
    line-height: 24px;
  }

  @media (min-width: @desktop-width) {
    width: 467px;
    margin-bottom: 27px;
    padding-right: 0;
  }
}

@media (max-width: @mobile-width-only) {
  .programm__description::after {
    content: "";
    position: absolute;
    top: 124%;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: @grey_light;
  }
}

