.main-nav {
  @media (min-width: 321px) {
    .offset(0; 0 auto);
  }

  @media (min-width: @tablet-width) {
    .offset(16px 0 0 0; 0 auto 24px 0);
  }
}

.main-nav__list {
  display: block;
  .offset(0; 0);
  list-style-type: none;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
  }
}

.main-nav__button-toggle {
  position: absolute;
  top: 22px;
  right: 23px;
  display: block;
  width: 18px;
  height: 19px;
  cursor: pointer;
  .border(0; 0; 0; 0);
  background-color: transparent;
  z-index: 2;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.main-nav--closed .main-nav__button-toggle::before {
  content: "";
  position: absolute;
  width: 18px;
  height: 2px;
  top: 9px;
  right: 0;
  box-shadow: 0 -6px @black, 0 6px @black;
  background-color: @black;
  z-index: 10;
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}

.main-nav--opened .main-nav__button-toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline y1='-0.75' x2='24.7577' y2='-0.75' transform='matrix(0.686589 -0.727046 0.686589 0.727046 2.00171 20)' stroke='black' stroke-width='1.5'/%3E%3Cline y1='-0.75' x2='24.7577' y2='-0.75' transform='matrix(0.686589 0.727046 -0.686589 0.727046 1 2)' stroke='black' stroke-width='1.5'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  z-index: 10;
}

@media (max-width: @mobile-width-only) {
  .main-nav--no-js .main-nav__button-toggle {
    display: none;
  }

  .main-nav--no-js .main-nav__list {
    display: block;
  }
}

.main-nav__item {
  position: relative;
  .offset(22px 50px 24px 49px; 0);

  @media (min-width: @tablet-width) {
    padding: 0;
    margin-right: 35px;

    &:nth-child(2) {
      margin-right: 37px;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  @media (min-width: @desktop-width) {
    .offset(0; 0 35px 0 0);

    &:nth-child(2) {
      margin-right: 33px;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}

.main-nav__item:first-child {
  padding: 24px 102px;

  @media (min-width: @tablet-width) {
    padding: 0;
    margin-right: 34px;
  }
}

@media (max-width: @mobile-width-only) {
  .main-nav__item::after {
    content: "";
    position: absolute;
    top: 66px;
    left: -20px;
    width: 320px;
    height: 1px;
    background-color: @special_grey-4;
  }

  .main-nav__item:nth-child(2)::after {
    top: 64px;
  }
}

.main-nav__link {
  display: block;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: @black;

  @media (min-width: @tablet-width) {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }

  @media (min-width: @desktop-width) {
    font-size: 20px;
    line-height: 30px;
  }
}

.main-nav__link--color {
  @media (min-width: @desktop-width) {
    color: @white;
  }
}
